.logo-nav-wrapper {
	display: flex;
	justify-content: space-between;
	text-transform: uppercase;
	@include bebas;
	position: relative; }

.logo {
	font-size: 44px;
	position: relative;

	&__link {
		display: flex;
		justify-content: center;
		align-items: center;
		text-decoration: none;
		color: $main-color;
		height: 100%;
		font-weight: bold;
		letter-spacing: 2px;
		margin-left: 1px;

		span {
			color: $secondary-color; } } }

.menu {
	// opacity: 0

	&__item {
		display: inline-block;
		margin: 17px 0 0 25px;
		letter-spacing: 1px; }

	&__link {
		color: #b9b9b9;
		text-decoration: none;
		font-size: 19px;
		font-weight: bold;

		&:hover {
			color: $main-color; }

		&_active {
			color: $main-color; } } }

.blog {
	height: 61px;
	background: $main-color;
	margin-top: 16px;
	position: relative;
	margin-bottom: 24px;

	&:before, &:after {
		position: absolute;
		content: "";
		height: 61px;
		background: #f2ae9c;
		width: 1000px;
		top: 0; }

	&:before {
		right: 100%;
		margin-right: 10px; }

	&:after {
		left: 100%;
		margin-left: 10px; }

	&__title {
		text-transform: uppercase;
		color: #fff;
		@include bebas;
		font-size: 30px;
		padding: 15px 0 0 18px;
		letter-spacing: 1px; } }

.main {
	width: 670px; }

.block {
	width: 670px;
	position: relative; }

.block-img {
	height: 300px;

	// &_img1
