.block-content {
	display: flex;
	margin-top: 20px; }

.block-info {
	width: 100%;
	margin: 2px 0 0 1px;

	&__title {
		color: $main-color;
		text-transform: uppercase;
		@include bebas;
		font-size: 16px; }

	&__text {
		color: #bababa;
		@include helvetica;
		font-size: 12px;
		margin: 7px 0 13px 0; } }

.block-text {
	margin-left: 35px;

	&__title {
		color: $main-color;
		text-transform: uppercase;
		@include bebas;
		font-size: 21px;
		letter-spacing: 1px; }

	&__text {
		color: #414141;
		@include helvetica;
		font-size: 12px;
		margin-top: 17px;
		line-height: 17px; }

	&_alone {
		margin-left: 0; } }

.blockquote {
		padding: 30px 50px 0 80px;
		width: 654px;
		height: 75px;
		background: url('../img/bg-quote.jpg') #f5f5f5;
		@include helvetica;
		font-style: italic;
		font-size: 12px;
		color: #474747;
		position: relative;

		&:before {
			position: absolute;
			content: "";
			width: 25px;
			height: 20px;
			background-image: url('../img/quote1.png');
			top: 25px;
			left: 36px;
			background-position: center center;
			background-repeat: no-repeat; } }

