.buttons {
	display: flex;

	&__link {
		display: inline-block;
		text-decoration: none;
		color: #fff;
		@include bebas;
		margin-right: 5px; }

	&__item {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 26px;
		height: 26px;
		background-color: $secondary-color;

		&_active {
			background: $main-color; } } }

.aside {
	width: 220px;
	margin-left: 52px; }

.small-block {
	width: 100%;
	margin-bottom: 30px;

	&__text {
		color: #414141;
		@include helvetica;
		font-size: 12px;
		margin-top: 17px;
		line-height: 15px; }

	&__link {
		display: block;
		text-decoration: none;
		color: #414141;
		@include helvetica;
		line-height: 15px; } }

.title-bg {
	display: flex;

	&__title {
		color: $main-color;
		text-transform: uppercase;
		@include bebas;
		font-size: 16px;
		letter-spacing: 1px;
		white-space: nowrap;
		margin: 2px 15px 0 0;
		letter-spacing: 1.3px; }

	&__bg {
		width: 100%;
		height: 13px;
		background: url('../img/line.png');
		display: block;
		margin-top: 4px; } }

.gallery {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	grid-gap: 5px;
	margin-top: 18px;

	&__img {
		background-color: $secondary-color;
		// border: 1px solid $main-color
		width: 50px;
		height: 50px;
		filter: grayscale(100%);
		transition: 0.3s;

		&:hover {
			filter: grayscale(0);
			transform: scale(0.97); } } }

